import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilFindInPage, cilPencil } from '@coreui/icons'
import { CContainer, CRow, CCol, CButton, CFormInput } from '@coreui/react'
import { quotationLists } from '../../mocks/quotationData'
import Pagenate from '../Share/Pagenate'
// import axios from 'axios'

const QuotationList = () => {
  const [data, setData] = useState([])
  const [currentPage, setCurrentPage] = useState(0)
  const [totalPages, setTotalPages] = useState(0)
  const itemsPerPage = 10

  useEffect(() => {
    // axios.get('https://jsonplaceholder.typicode.com/posts/').then((response) => {
    setData(quotationLists)
    setTotalPages(Math.ceil(quotationLists.length / itemsPerPage))
    // })
  }, [])

  const startIndex = currentPage * itemsPerPage
  const endIndex = startIndex + itemsPerPage
  const subset = data.slice(startIndex, endIndex)

  const handlePageChange = (selectedPage) => {
    setCurrentPage(selectedPage.selected)
  }
  return (
    <div className="lists-container px-3">
      <CContainer>
        <div className="form-box">
          <CRow className="mb-3">
            <CCol md={2}>
              <CFormInput label="報價單編號" placeholder="請輸入報價單編號" />
            </CCol>
            <CCol md={2}>
              <CFormInput label="客戶統編" placeholder="請輸入客戶統編" />
            </CCol>
            <CCol md={2}>
              <CFormInput type="date" label="起始日期" placeholder="請輸入日期" />
            </CCol>
            <CCol md={2}>
              <CFormInput type="date" label="結束日期" placeholder="請輸入日期" />
            </CCol>
            <CCol md={2} className="d-flex align-items-end">
              <CButton color="primary">搜尋</CButton>
            </CCol>
          </CRow>
        </div>
        <div className="list-head">
          <CRow className="align-items-center">
            <CCol sm={2}>報價單編號</CCol>
            <CCol sm={2}>客戶名稱</CCol>
            <CCol sm={2}>客戶統編</CCol>
            <CCol sm={2}>建立時間</CCol>
            <CCol sm={1}>狀態</CCol>
            <CCol className="d-flex justify-content-center" sm={1}>
              編輯
            </CCol>
            <CCol className="d-flex justify-content-center" sm={2}>
              預覽/下載
            </CCol>
          </CRow>
        </div>
        <div className="list-body">
          {subset &&
            subset.length > 0 &&
            subset.map((item, i) => {
              return (
                <div className="list-item" key={item.num + i}>
                  <CRow className="align-items-center">
                    <CCol sm={2}>{item.num}</CCol>
                    <CCol sm={2}>{item.client}</CCol>
                    <CCol sm={2}>{item.taxID}</CCol>
                    <CCol sm={2}>{item.createDate}</CCol>
                    <CCol sm={1}>{item.statusText}</CCol>
                    <CCol className="d-flex justify-content-center" sm={1}>
                      {item.status === 'A' && (
                        <Link to="/quotation/edit" className="action-icon">
                          <CIcon className="" icon={cilPencil} />
                        </Link>
                      )}
                    </CCol>
                    <CCol className="d-flex justify-content-center" sm={2}>
                      <Link to="/pdfContainer" className="action-icon">
                        <CIcon icon={cilFindInPage} />
                      </Link>
                    </CCol>
                  </CRow>
                </div>
              )
            })}
        </div>

        {data.length > itemsPerPage && (
          <Pagenate
            totalPages={totalPages}
            currentPage={currentPage}
            handlePageChange={handlePageChange}
          />
        )}
      </CContainer>
    </div>
  )
}

export default QuotationList
